import { useState, useEffect } from 'react';
import {
  Routes,
  Route
} from "react-router-dom";

import config from "./config/config.json";

import './App.scss';
import Header from './components/Header/Header';
import Poll from './components/Poll/Poll';
import Footer from './components/Footer/Footer';

import bg1 from './img/page-chicken.svg';
import bg2 from './img/page-milk.svg';
import bg3 from './img/page-sport.svg';
import bg4 from './img/page-calendar.svg';
import bg5 from './img/page-data.svg';
import bg6 from './img/page-budget.svg';
import bg7 from './img/page-time.svg';
import bg8 from "./img/page-sex.svg"
import Subscribe from './pages/subscribe/Subscribe';

function App(props) {
  const [bg, setBg] = useState(null);
  const [header, setHeader] = useState("    ,          .");
  const [pollSubTitle, setPollSubTitle] = useState('!');
  const [pollTitle, setPollTitle] = useState(' ');
  const [step, setStep] = useState(1);
  const [userData, setUserData] = useState({
    meat: null,
    milk: null,
    physical: null,
    day: null,
    sex: null,
    data: {
      age: null,
      height: null,
      weight: null,
      wishedWeight: null
    },
    budget: null,
    time: null
  });

  useEffect(() => {
    const stepData = [
      {
        bg: bg8,
        subTitle: "!",
        title: " "
      },
      {
        bg: bg1,
        subTitle: "",
        title: "   ?"
      },
      {
        bg: bg2,
        subTitle: " ",
        title: "     ?"
      },
      {
        bg: bg3,
        subTitle: " ",
        title: "     ?"
      },
      {
        bg: bg4,
        subTitle: "  ",
        title: "   "
      },
      {
        bg: bg5,
        subTitle: "",
        title: " "
      },
      {
        bg: bg6,
        subTitle: "",
        title: "     ()"
      },
      {
        bg: bg7,
        subTitle: "",
        title: "  "
      },
      {
        bg: null,
        subTitle: null,
        title: "  "
      },
      {
        bg: null,
        subTitle: null,
        title: "  "
      },
      {
        bg: null,
        subTitle: null,
        title: "    !"
      },
      {
        bg: null,
        subTitle: "",
        title: ""
      },
    ]
    let currentStep = step - 1;
    if (currentStep >= 1) {
      setHeader(null);
    }
    else { setHeader("    ,          .") }
    setBg(stepData[currentStep].bg);
    setPollSubTitle(stepData[currentStep].subTitle);
    setPollTitle(stepData[currentStep].title);
  }, [step])

  async function sendData(type = "payment", data, option = false) {
    let parameters = type;
    if (option && userData.id) parameters += `/${userData.id}/${option}`;
    try {
      const response = await fetch(`${config.SERVER.url}/${parameters}`, {
        method: "POST",
        headers: config.REQUEST_HEADERS,
        body: JSON.stringify(data)
      });
      let res = await response;
      if (res.ok && option === !!false) {
        let answer = res.json();
        return answer;
      }
      else if (res.ok) {
        return true;
      }
    } catch (error) {
      console.warn("Error in sendData:");
      console.error(error);
      return null;
    }
  }

  function bgPic() {
    if (bg) {
      return (
        <picture className={"page-img fade" + anim}>
          <img src={bg} alt="" />
        </picture>
      )
    }
  }

  function bgIcons() {
    if (step >= 1 && step <= 8) {
      return (
        <div className="bg-icons"></div>
      )
    }
    else { return null }
  }

  const [anim, setAnim] = useState(" fade");

  useEffect(() => {
    setAnim("");
    setTimeout(() => {
      setAnim(" fade-in")
    }, 400);
    return () => { setAnim(" fade") }
  }
    , [step]);

  return (
    <div className='wrapper'>
      <div className="wrapper__inner">
        <Header description={header} step={step} />
        <Routes>
          <Route path="/" element={
            <Poll
              subtitle={pollSubTitle}
              title={pollTitle}
              step={step}
              setStep={setStep}
              userData={userData}
              setUserData={setUserData}
              anim={anim}
              sendData={sendData} />}>
          </Route>
          <Route path="unsubscribe" element={<Subscribe />}></Route>
        </Routes>
      </div>
      {bgPic()}
      {bgIcons()}
      <Footer />
    </div>
  );
}

export default App;